import React, { useEffect } from "react";
import Project from "./Project/Project";
import splash from "./splash.png";
import todo from "./project-imgs/todo.png";
import recipe from "./project-imgs/recipe.png";
import weather from "./project-imgs/weather.png";
import meme from "./project-imgs/meme.png";
import stab from "./project-imgs/stab.png";

import AOS from "aos";
import "aos/dist/aos.css";

export default function Projects() {
  useEffect(() => {
    AOS.init();
    AOS.refresh();
  });
  return (
    <div className="Projects">
      <div
        data-aos="zoom-in"
        className="projectTite-container"
        id="projectTitle"
      >
        <img src={splash} alt="paint-spash" />
        <h1 className="Projects-title">Projects</h1>
      </div>

      <div id="projects"></div>

      <div className="project-container">
        <Project
          img={stab}
          title={"Stabofficial"}
          desc={
            "I built this page for my brother's friends who just started their own filming business. They were very happy about their new webpage!"
          }
          demoLink="http://www.stabofficial.hu/"
          codeLink="https://github.com/SzathRobi/stabofficial"
          aosEffect="zoom-out-right"
        />
        <Project
          img={weather}
          title={"Current Weather App"}
          desc={
            "A React weather app which uses Current Weather Data API to get weather data. I am also using hooks to manage states and side effects."
          }
          demoLink="https://szathrobi.github.io/Currrent-Weather-App/"
          codeLink="https://github.com/SzathRobi/Currrent-Weather-App/tree/master"
          aosEffect="zoom-out-down"
        />
        <Project
          img={todo}
          title={"Todo List"}
          desc={
            "What a surprise! A React todo app! You can make todos, group them if it is needed and dont worry, LocalStorage take care about saving your todos."
          }
          demoLink="https://szathrobi.github.io/todo-list/"
          codeLink="https://github.com/SzathRobi/todo-list/tree/master"
          aosEffect="zoom-out-left"
        />

        <Project
          img={meme}
          title={"Meme Generator"}
          desc={
            "In this React app you can choose from some meme templates, add some text to, position it as you wish. Finally with a simple right click you can download your meme"
          }
          demoLink="https://szathrobi.github.io/meme-generator/"
          codeLink="https://github.com/SzathRobi/meme-generator/tree/master"
          aosEffect="zoom-in-left"
        />
        <Project
          img={recipe}
          title={"Recipe App"}
          desc={
            "A React recipe app where you can bookmark your favourite recipes and automaticly save it!"
          }
          demoLink="https://szathrobi.github.io/recipe-app/"
          codeLink="https://github.com/SzathRobi/recipe-app/tree/master"
          aosEffect="zoom-in-up"
        />
      </div>
    </div>
  );
}
